		async onLoad() {
			//获取屏幕宽度
			windowWidth = uni.getSystemInfoSync().windowWidth;
		},
		methods: {
			//tab切换,这里传过来的e是id
			async changeTab(e) {
				if (scrollTimer) {
					//多次切换只执行最后一次
					//clearTimeout：取消由 setTimeout 设置的定时器。
					clearTimeout(scrollTimer);
					scrollTimer = false;
				}
				let index = e;
				//e=number为点击切换，e=object为swiper滑动切换
				if (typeof e === 'object') {
					index = e.detail.current
				}
				if (typeof tabBar !== 'object') {
					//tabBar是上面定义的参数，getElSize是获取元素的size，nav-bar是选项卡
					tabBar = await this.getElSize("nav-bar")
				}
				//计算宽度相关
				let tabBarScrollLeft = tabBar.scrollLeft;
				let width = 0;
				let nowWidth = 0;
				//获取可滑动总宽度
				for (let i = 0; i <= index; i++) {
					let result = await this.getElSize('tab' + i)
					//result.width:每个选项框的宽度
					width += result.width;
					if (i === index) {
						nowWidth = result.width;
					}
				}
				if (typeof e === 'number') {
					//点击切换时先切换再滚动tabBar，避免同时切换视觉错位
					this.tabCurrentIndex = index;
				}
				//延迟300ms，等待swiper动画结束再修改tabBar
				//setTimeout(callback, delay, rest)：设定一个定时器。在定时到期以后执行注册的回调函数
				/*
				callback：回调函数 
				delay：延迟的时间，函数的调用会在该延迟之后发生，单位 ms（设置时间的长短会影响动画的快慢）
				rest：param1, param2, ..., paramN 等附加参数，它们会作为参数传递给回调函数
				*/
				scrollTimer = setTimeout(() => {
					//width:每个选项卡叠加之后的宽度 nowWidth：每个选项卡的宽度
					if (width - nowWidth / 2 > windowWidth / 2) {
						//如果当前项越过中心点，将其放在屏幕中心
						this.scrollLeft = width - nowWidth / 2 - windowWidth / 2;
					} else {
						this.scrollLeft = 0;
					}
					if (typeof e === 'object') {
						this.tabCurrentIndex = index;
					}
					this.tabCurrentIndex = index;
					//第一次切换tab，动画结束后需要加载数据
					let tabItem = this.tabBars[this.tabCurrentIndex];
					if (this.tabCurrentIndex != 0 && tabItem.loaded !== true) {
						// this.loadNewsList('add');
						tabItem.loaded = true;
					}
				}, 300)
			},
			//获得元素的size
			getElSize(id) {
				return new Promise((res, rej) => {
					let el = uni.createSelectorQuery().select('#' + id);
					el.fields({
						size: true,
						scrollOffset: true,
						rect: true
					}, (data) => {
						res(data);
					}).exec();
				});
			},
			//跳转到订单详情
			navToOrderDetail(items) {
				let data = {

				}
